@import "../../App.module.scss";




.Navbar{
    border: 0;
    padding: 0 10rem;
    width: 100vw;
    height: 3rem;
    box-sizing: border-box;
    display: inline-flex;
    justify-content: space-between;
    position: absolute;
    top: 0;
    background-color: trans;
    color: $primaryLight;
    z-index: 1000;
    font-size: 1.2rem;

    .Blank{
        display: none;
        margin: auto;
    }
    
    .Head{
        font-family: 'Monoton';
        font-size: 1.8rem;
        margin: auto 0;
        display: flex;
        justify-content: center;

            color: $primaryLight;
            text-decoration: none ;
    
    }

    .Navlinks{
        padding: 0;
        margin: auto;
        list-style-type: none;
        display: inline-flex;
        justify-content: space-around;
        
        li{
            margin: 0;
            padding: 1rem 1.5rem;
            border: 1px solid transparent ;
            
            .Navs{
                padding: 0;
                margin: 0;
                color: $primaryLight;
                text-decoration: none ;
                font-size: 1.1rem;
                font-weight: lighter;
            }

            .Navs:hover{
                color: #fff;
            }
            
            .ActiveNavs{
                margin: 0;
                padding: 0;
                text-decoration: overline;
                font-weight: normal;
                color: #fff;
                width: 100%;
                height: 100%;
            }
            
            p{
                padding: 0;
                margin: 0;
                
            }
        }
        
    }
}




.Toggle{
    position: relative;
    width: 2.8rem;
    height: 1.3rem;
    margin: auto 0;
    border-radius: 40px;
    outline: none;

    input[type="checkbox"]{
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0px;
        left: 0px;
        right: 0px;
        bottom: 0px;
        margin: 0px;
        opacity: 0;
        cursor: pointer;
        z-index: 2;
    }
    
    span{
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        overflow: hidden;
        opacity: 1;
        background-color: #2ac0fc;
        //box-shadow: 0 1rem 5rem #838383;
        border-radius: 40px;
        transition: 0.2s ease background-color, 0.2s ease opacity;
    }
    
    span:before,
    span:after{
        content: '';
        position: absolute;
        top: 0.2rem;
        width: 0.9rem;
        height: 0.9rem;
        border-radius: 50%;
        transition: 0.5s ease transform, 0.2s ease background-color;
    }
    
    span:before{
        background-color: rgb(248, 144, 59);
        transform: translate(0.3rem,0rem);
        z-index: 2;
    }
    
    span:after{
        background-color: #41c7fc;
        transform: translate(0.3rem,0rem);
        z-index: 1;
    }
    
    input[type="checkbox"]:checked + span{
        background-color: #000000;
    }
    
    input[type="checkbox"]:active + span{
        opacity: 0.5;
    }
    
    input[type="checkbox"]:checked + span:before{
        background-color: #000;
        transform: translate(1.3rem,-0.3rem);
    }
    
    input[type="checkbox"]:checked + span:after{
        background-color: #fff;
        transform: translate(1.6rem,-0.05rem);
    }
}

@media (max-width: 800px) {
    .Navbar{
        height: 0;
        width: 100vw;
        padding: 1rem;
        box-sizing: border-box;
        
        .Blank{
            display: flex;
        }

        .Head{
            margin: auto;
        }
    
        .Navlinks{
            display: none;
        }
    }

    .Toggle{
        margin: 0 auto;
        margin-right: 1rem;
    }

}





