.Banner{
    margin: auto;
    border: 0;
    padding: 0;
    padding-top: 4rem;
    display: grid;
    width:80vw;
    height: 100vh;
    color: #f5f3f3;
    z-index: 500;
    box-sizing: border-box;
    justify-content: space-between;
    grid-template-columns: 6fr 1fr 1fr ;
    grid-template-rows: 1fr;
    scroll-snap-align: start;

    .Heading{
        padding: 0 0 4rem 4rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        
        .Hello{
            margin: 1rem;
            letter-spacing: 0.1rem;
        }
        
        .Name{
            margin: 1rem;
            font-size: 2.8rem;
            font-weight: lighter;

            br{
                display: none;
            }
        }

        .Desc{
            margin: 1rem;
            letter-spacing: 0.3rem;
            //font-style: italic;

        }
    }
    
    .Scroll{
        text-align: center;
        width: 3rem;
        bottom: 5rem;
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: flex-end;
        grid-row-end: span;
        
    
        p {
            text-align: center;
            writing-mode: vertical-rl;
            text-orientation: right;
            letter-spacing: 1rem;
            font-weight: lighter;
          }
        
        img{
            /* display: flex;
            justify-content: center; */
            height: 7rem;
            z-index: 600;
            padding-bottom: 3rem;
            
        }
    }
    
    .Social{
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding-bottom: 2rem;

        Link{
            width: 0%
        }
        
        .Container{
            display: flex;
            flex-direction: column;
            justify-content: center;
            padding: 1rem 0.2rem;
            width: 4.2rem;
            
        }
        
        .Container:hover{
            background-color: #dfdfdf41;
        }
        
        img{
            margin: 0.6rem 1rem;
            height: 2rem;
            width: auto;
        }
    }
}

@media (min-width: 1800px) {
    .Banner > .Heading{

        .Name{
            font-size: 3.2rem;
        }
    }
}

@media (max-width: 800px) {
    .Banner{
        grid-template-columns: 1fr ;
        grid-template-rows: 4fr 1fr;
        padding-bottom: 3rem;

        .Heading{
            padding: 0;
            
            .Name{
                font-size: 2.5rem;
                
                br{
                    display: initial;
                }
            }
        }

        .Scroll{
            display: none;
        }

        .Social{
            margin: auto;
            .Container{
                width: fit-content;
                flex-direction: row;

            }
        }
    }

    @media (max-width: 390px) {
        .Banner > .Heading{
            margin-left: -1rem;

            .Name{
                font-size: 2.2rem;
            }
        }
    }
}



