@import "../../App.module.scss";

@mixin info {
    margin: auto;
    width: 100%;
    height: 75vh;
    position: relative;
    z-index: 500;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    overflow-y: scroll;
    
    p{
        width: 70%;
        margin: auto;
        letter-spacing: 0.1rem;
        line-height: 1.6rem;
    }

    /* .ImgContainer{
        margin: 0;
        border: 0;
        padding: 0;
        background-color: $primaryLight
    } */

    .Skills{
        margin: auto;
        text-align: center;
        font-weight: 600;
    }

    .ImgContainer{
        margin: auto;
        width: 100%;
        text-align: center;
    }

    img{
        margin: 1rem;
        height: 3rem;
        width: 3rem;
    }
    
    @media (max-width: 800px) {
        height: calc(100vh - 11rem);

        p{
            width: 75%;
            margin-top: 0;
        }
        
        
        img{
            margin: 1rem;
            height: 2.2rem;
            width: 2.2rem;
        }


        @media (max-width: 390px) {
            p{
                width: 80%;
                
            }

            img{
                margin-top: 0.8rem;
                height: 2rem;
                width: 2rem;
            }
        }
        
    }
}

.ScrollSnap{
    scroll-snap-align: start;
    height: 100vh;
    box-sizing: border-box;
}

.Header{
    text-align: center;
    margin: auto;
    border: 0;
    padding: 0;
    width:100vw;
    height: 25vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    color: #dfdfdf;
    z-index: 500;
    box-sizing: border-box;
    font-size: 4rem;
    
    h1{
        position: relative;
        top: 1rem;
    }
    
}

.InfoLight{
    @include info;
    
    background-color: $secondaryLight;
    color: $secondaryDark !important;

    img{
        filter: brightness(90%);
    }
}

.InfoDark{
    @include info;
    
    background-color: $secondaryDark;
    color: $secondaryLight;

    img{
        filter: brightness(140%);
    }
}


@media (max-width: 800px) {

    .Header{
        height: 11rem;
        
        h1{
            top: 1.5rem;
            
        }
    }
}