@import "../../App.module.scss";

.ScrollSnap{
    scroll-snap-align: start;
    height: 100vh;
    box-sizing: border-box;
    z-index: 500;
}

.Header{
    text-align: center;
    margin: auto;
    border: 0;
    padding: 0;
    width:100vw;
    height: 30vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    color: #dfdfdf;
    z-index: 500;
    box-sizing: border-box;
    font-size: 4rem;
    
    h1{
        position: relative;
        top: 1rem;
    }
    
}

.ContactContainer{
    border: 0;
    padding: 0rem 0rem 5rem 7rem;
    margin: auto;
    height: 75vh;
    width: fit-content;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-evenly;
    /* background-color: $secondaryLight;
    box-shadow: 0 0 5rem #00000065; */

    section{
        height: 100%;
        overflow-y: scroll;
    }

    a{
        border: 0;
        margin: 1rem 0;
        display: flex;
        align-items: center;
        text-decoration: none;
    
        img{
            height: 2rem;
            width: auto;
        }
        
        p{
            position: relative;
            display: flex;
            padding: 0 3rem;
            color: $primaryLight;
        }

        button{
            margin: auto;
            margin-top: 1rem;
            padding: 1rem 1.5rem;
            border: 0;
            background-color: $primaryLight;
            color: $secondaryDark;
            position: relative;
            right: 3rem;
        }
        
        button:hover{
            color: $primaryLight;
            border: 2px solid $secondaryLight;
            background-color: transparent;
            cursor: pointer;

        }
    }
}


@media (max-width: 800px) {
    .ScrollSnap{
        height: 100vh;
        box-sizing: border-box;
    }
    
    .Header{
        height: 11rem;
        
        h1{
            top: 1.5rem;
            
        }
    }

    .ContactContainer{
        margin: auto;
        border: 0;
        padding: 0;
        display: flex;
        justify-content: center;
        align-items: center;

        section{
            text-align: center;
            padding-left: 2rem;
        }
        
        a{
            p{
                position: relative;
                right: 1rem;
            }

            button{
                margin-top: 2rem;
                right: 1rem;
            }
        }
    }
}

