@import url(https://fonts.googleapis.com/css?family=Montserrat);


$backgroundImage: "https://source.unsplash.com/ZeM0C96lmdU/1920x1920";

$primaryLight: #ececec;
$primaryDark: #141414;
$secondaryLight: #cecece;
$secondaryDark: #1f1e1e;

@mixin app {
  width: 100vw;
  box-sizing: border-box;
  background-blend-mode: overlay;
  scroll-behavior: smooth;

  scroll-snap-type: y mandatory;
    overflow-x: hidden;
    overflow-y: scroll;
    height: 100vh;
    box-sizing: border-box;
}



.AppLight {
  @include app;
  
  background: url($backgroundImage), #0d3185e7;
  background-size: cover;
  background-attachment: fixed;
  //background-color: #ccc8c8;
  color: #1f1e1e;
  
}

.AppDark {
  $bgColor: #1f1e1e;
  $fgColor: #cecece;


  @include app;
  background: url($backgroundImage), #021642e7;
  background-size: cover;
  //background-color: #1f1e1e;
  background-attachment: fixed;
  color: $secondaryDark;
}

h1{
  font-family: 'Monoton', cursive,'Cinzel', sans-serif;
  font-size: 2.5rem;
  letter-spacing: 0.4rem;
  font-weight: lighter;
}

p{
  font-family: 'Spartan', sans-serif;
  font-size: 1rem;
}

@media (min-width: 1800px) {

  h1{
    font-size: 2.8rem;
  }

  p{
    font-size: 1.3rem;
  }
}

@media (max-width: 800px) {
  

  h1{
    font-size: 2rem;
    font-weight: lighter;
  }
  
  p{
    font-size: 0.9rem;
  }

  @media (max-width: 390px){

    h1{
      font-size: 1.8rem;
    }

    p{
      font-size: 0.8rem;
    }
  }
}