@import "../../App.module.scss";

@mixin info {
    margin: auto;
    width: 100%;
    height: 75vh;
    background-color: $secondaryLight;
    position: relative;
    z-index: 500;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
    .Cards{
        padding: 1rem;
        width: 90%;
        height: 55vh;
        display: grid;
        /* grid-template-columns: repeat(auto-fill, 350px ); */
        grid-template-columns: repeat(auto-fill,350px);
        justify-content: center;
        overflow-y: scroll;
        scrollbar-width: none;
    
        ::-webkit-scrollbar{
            display: none;
        }
    
    
        .Card{
            margin: 1rem;
            height: 250px;
            
            .Logo{
                margin: 0;
                width: 100%;
                height: 50%;
                background-color: $primaryLight;
                display: flex;
                align-items: center;
                justify-content: center;
    
                img{
                    height: 60%;
                    width: auto;
                }
            }
    
            .Description{
                margin: 0;
                padding: 1rem 0;
                border: 0;
                text-align: center;
                font-family: 'Spartan', sans-serif;
                font-size: x-large;
                color: $secondaryLight;
                display: flex;
                flex-direction: column;
                justify-content: space-around;
                height: 50%;
                box-sizing: border-box;
    
                p{
                    margin: 0;
                    border: 0;
                    padding: 0;
                }
                
                Link{
                    padding: 0;
                    margin: auto;
                    border: 0;
                    text-decoration: none;
                }
    
                button{
                    margin: 0;
                    border: 0;
                    padding: 0.5rem 1rem;
    
                }
                
                button:hover{
                    color: #dfdfdf;
                    background-color: #086bdb;
                    cursor: pointer;
                }
            }
            
            
        }
    }
    
    @media (max-width: 800px) {
        height: calc(100vh - 11rem);
    }
}

.ScrollSnap{
    scroll-snap-align: start;
    height: 100vh;
    box-sizing: border-box;
}

