.Header{
    text-align: center;
    margin: auto;
    border: 0;
    padding: 0;
    width:100vw;
    height: 25vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    color: #dfdfdf;
    z-index: 500;
    box-sizing: border-box;
    font-size: 4rem;
    
    h1{
        position: relative;
        top: 1rem
    }
    
}

.InfoLight{
    @include info;

    background-color: $secondaryLight;

    .Card{
        background-color: $secondaryDark;
        box-shadow: 0.3rem 0.3rem 1rem #7a7a7a;
    }

    p{
        color: $primaryLight
    }

    button{
        background-color: $primaryLight;
        color: $secondaryDark;
    }
}

.InfoDark{
    @include info;

    background-color: $secondaryDark;

    .Card{
        background-color: $secondaryLight;
        box-shadow: 0.3rem 0.3rem 1rem #000000;
        
        p{
            color: $primaryDark
        }
    
        button{
            background-color: $secondaryDark;
            color: $secondaryLight;
        }
    }

}

@media (max-width: 800px) {
    .Header{
        height: 11rem;

        h1{
            top: 1.5rem;
            
        } 
    }
}




